.footer {
  display: flex;                  /* establish flex container */
  flex-direction: row;            /* default value; can be omitted */
  flex-wrap: nowrap; 
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: #C3C3C3;
    width: 100%;
    height: 30px;
    margin: 0;
  }

  .margin{
    margin: 0;
  }
  .title {
    margin: 0;
    top: 50%;
    color: #FFFFFF;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    padding-left: 3px;
  }