
.window {
    height: 500px;
    width: 500px;
    background-color: #C1C0C1;
    border-top: #FFFFFF 3px solid;
    border-left: #FFFFFF 3px solid;
    border-bottom: #000000 3px solid;
    border-right: #000000 3px solid;
  }

  button{
    float: right;
    margin-left: auto;
    margin-right: 3px;
    margin-top: 3px;
    height: 20px;
    width: 20px;
    background-color: #C1C0C1;
    border-top: #FFFFFF 3px solid;
    border-left: #FFFFFF 3px solid;
    border-bottom: #000000 3px solid;
    border-right: #000000 3px solid;
    text-align: center;
    left: 205px;
    top: 2.5px;
    font-size: 10px;
    padding: 0px;
  }
  
  .app {
    background-color: #C3C3C3;
    height: 95%;
    width: 100%;
    position:absolute;
}

  .title {
    margin: 0;
    top: 50%;
    color: #FFFFFF;
    position: relative;
    top: 50%;
    padding-left: 3px;
  }

  .banner {
    display: flex;                  /* establish flex container */
    flex-direction: row;            /* default value; can be omitted */
    flex-wrap: nowrap; 
    height: 25px;
    width: 100%;
    background-color: #000888;
    top: 0;
  }

  .content{
      padding-left: 5px;
  }

  .application{
    position: absolute;
  }